.form-field {
   &__input {
      width: 100%;
      height: 64px;
      padding: 0 22px;
      box-shadow: 0 1px 30px 1px rgba(4, 4, 4, 0.31);
      border: 1px solid #ffffff;
      border-radius: 10px;
      background-color: #3727a3;
      @include f('dp400', 23, 64);
      color: #fff;
      transition: .3s ease-in-out;
      @include placeholder-color(#fff); } }

.textarea {
   display: block;
   width: 100%;
   height: 242px;
   padding: 23px 26px;
   resize: none;
   @include f('dp400', 23, 64);
   transition: .3s ease-in-out;
   box-shadow: 0 1px 30px 1px rgba(4, 4, 4, 0.31);
   border-radius: 10px;
   border: 1px solid #ffffff;
   background-color: #3727a3;
   color: #fff;
   @include placeholder-color(#fff); }
