  padding: 35px 0;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  position: relative;
}

.header__logo::before {
  position: absolute;
  top: -4px;
  right: 33px;
  width: 7px;
  height: 7px;
  background-color: #e24b2e;
  border-radius: 50%;
  content: '';
}

.header__logo-label {
  position: absolute;
  top: -25px;
  transform: scaleY(-1);
  right: 16px;
  writing-mode: vertical-lr;
  font-family: "DINPro", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
