}

@media only screen and (max-width: 767px) {
  .header__nav {
    flex-direction: column;
    justify-content: center;
    position: absolute;
    top: 130px;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    padding-bottom: 130px;
    overflow: auto;
    opacity: 0;
    border: 0;
    visibility: hidden;
    transform: translate3d(0, 100px, 0);
    transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
    background: #fff;
    text-align: center;
    box-shadow: inset 0 0 10px rgba(102, 102, 102, 0.5);
  }
}

.header__nav.is-active {
  opacity: 1;
  visibility: visible;
  transform: translate3d(0, 0, 0);
  transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease;
}

.header__nav-list {
  display: flex;
  justify-content: space-between;
  margin-right: 14px;
}

@media only screen and (max-width: 1023px) {
  .header__nav-list {
    margin-right: 0;
    padding-bottom: 4px;
  }
}

@media only screen and (max-width: 767px) {
  .header__nav-list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: auto;
    height: 100%;
    padding-bottom: 0;
  }
}

.header__nav-list li {
  position: relative;
}

.header__nav-list li::after, .header__nav-list li::before {
  pointer-events: none;
  content: '';
  position: absolute;
  top: calc(100% + 10px);
  left: calc(50% - 4px);
  width: 8px;
  height: 8px;
  background-color: #e24b2e;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.3s ease;
}

.header__nav-list li:hover::before {
  opacity: 1;
  left: calc(50% - 20px);
}

.header__nav-list li:hover::after {
  opacity: 1;
  left: calc(50% + 12px);
}

.header__nav-list li:not(:last-child) {
  margin-right: 30px;
}

@media only screen and (max-width: 767px) {
  .header__nav-list li:not(:last-child) {
    margin-bottom: 30px;
  }
}

.header__nav-list li a {
  position: relative;
  display: block;
  font-size: 15px;
  line-height: 15px;
  text-transform: uppercase;
  color: #fff;
}

.header__nav-list li a::before {
  pointer-events: none;
  content: '';
  position: absolute;
  top: calc(100% + 10px);
  left: calc(50% - 4px);
  width: 8px;
  height: 8px;
  background-color: #e24b2e;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.3s ease;
}

@media only screen and (max-width: 1023px) {
  .header__nav-list li a {
    font-size: 12px;
  }
}

@media only screen and (max-width: 767px) {
  .header__nav-list li a {
    font-family: Arial, sans-serif;
    font-size: 16px;
  }
}

.header__nav-list li a:hover::before {
  opacity: 1;
}

.header__nav-btn {
  display: none;
  position: relative;
  top: -5px;
  border: 0;
  outline: 0;
  padding: 0;
  z-index: 11;
}

@media only screen and (max-width: 767px) {
  .header__nav-btn {
    display: block;
  }
}

.header__nav-btn.is-active span {
  color: #666666;
}

.header__nav-btn-burger {
  display: block;
  margin: auto;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.header__nav-btn-lines {
  display: block;
  width: 40px;
  height: 3px;
  position: relative;
  transform: translateY(25px);
  background: #fff;
  transition: all .3s;
}

.header__nav-btn-lines:after, .header__nav-btn-lines:before {
  width: 40px;
  height: 3px;
}

.header__nav-btn.is-active .header__nav-btn-lines {
  background: rgba(255, 255, 255, 0);
}

.header__nav-btn-lines:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 10px;
  background: #fff;
  transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.header__nav-btn-lines:after {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
  background: #fff;
  transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.header__nav-btn.is-active .header__nav-btn-lines:after {
  top: 0;
  transform: rotate(45deg);
  transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.header__nav-btn.is-active .header__nav-btn-lines:before {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

