.earn__container {
  position: relative;
  z-index: 1;
}

.partner {
  position: relative;
  padding: 67px 0 84px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.partner::after {
  opacity: .95;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(to bottom right, #ce462c, #f8af2f);
  content: '';
}

.partner__container {
  position: relative;
  z-index: 1;
}

.partner__section-title {
  margin-bottom: 30px;
}

.partner__slider .slick-list {
  margin: 0 -15px;
}

.partner__slider .slider-dots {
  margin-top: 60px;
}

.partner__slider-item {
  padding: 15px;
  color: #fff;
}

.partner__slider-item-inner {
  padding: 20px 30px;
  box-shadow: 0 0 27px 1px rgba(226, 75, 46, 0.24);
  border-radius: 10px;
  background-image: linear-gradient(-49deg, #e24b2e 0%, #e24b2e 100%);
}

.partner__item-title {
  margin-bottom: 15px;
  font-family: "DINPro-Medium", Arial, sans-serif;
  font-weight: normal;
  font-size: 23px;
  line-height: 40px;
}

.partner__item-descr {
  font-size: 16px;
  line-height: 23px;
  font-family: "DINPro", Arial, sans-serif;
  font-weight: normal;
}

.news {
  padding: 85px 0 105px;
  background-image: linear-gradient(-28deg, #3b1eaa 0%, #5934a2 100%);
}

.news__container {
  max-width: 1200px;
}

.news__slider .slick-list {
  margin: 0 -15px;
}

.news__slider .slider-btn_prev {
  left: -70px;
}

.news__slider .slider-btn_next {
  right: -70px;
}

.news__item {
  margin: 0 15px;
  color: #fff;
}

.news__item-img-wrap {
  margin-bottom: 30px;
  overflow: hidden;
  border-radius: 15px;
  position: relative;
}

.news__item-img-wrap:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 72.16216%;
}

.news__item-img-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover;';
}

.news__item-img-wrap img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.news__item-content {
  display: flex;
  align-items: flex-start;
  font-size: 16px;
  line-height: 22px;
}

.news__item-text {
  flex: 0 1 calc(100% - 70px);
  padding-right: 30px;
  font-size: 16px;
  line-height: 22px;
}

.news__item-date {
  flex: 0 0 70px;
  width: 70px;
  padding: 5px;
  border-radius: 10px;
  border: 1px solid #eaa12e;
  text-align: center;
}

.news__item-date span {
  display: block;
  color: #eaa12e;
}

.begin {
  padding: 60px 0;
  background-color: #3727a3;
}

.begin__top-text {
  margin-bottom: 50px;
  text-align: center;
}

.begin__top-descr {
  margin-top: 20px;
  font-family: "DINPro", Arial, sans-serif;
  font-weight: normal;
  font-size: 23px;
  line-height: 28px;
  color: #fff;
}

.begin__form-field:not(:last-child) {
  margin-bottom: 25px;
}

.begin__actions {
  max-width: 1024px;
  margin: 30px auto 0;
  text-align: center;
}

.begin__actions-agree {
  margin-bottom: 15px;
  font-family: "DINPro", Arial, sans-serif;
  font-weight: normal;
  font-size: 13px;
  line-height: 28px;
  color: #fff;
}
